import { Component, OnInit } from '@angular/core';
import { PlatosService } from '../platos.service';
import { menuColectivo } from '../platos';

@Component({
  selector: 'app-entrantes',
  templateUrl: './entrantes.component.html',
  styleUrls: ['./entrantes.component.css']
})
export class EntrantesComponent implements OnInit {
   
  menus: menuColectivo[];

  constructor(private platosService: PlatosService) { }
  
 getMenus():void{
    this.platosService.getMenus().subscribe(data=>this.menus=data)
 }

  ngOnInit() {
    this.getMenus();
  }

}
