import { Component, OnInit } from '@angular/core';

import { menuIndividual } from "../platos";
import { PlatosService } from '../platos.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-detalle-menu',
  templateUrl: './detalle-menu.component.html',
  styleUrls: ['./detalle-menu.component.css']
})
export class DetalleMenuComponent implements OnInit {

  menu: menuIndividual;

  constructor( private platosService: PlatosService, private ruta: ActivatedRoute) { }

  getMenu():void{
    const ID = +this.ruta.snapshot.paramMap.get('id');
    this.menu = this.platosService.getMenu(ID);
  }

  ngOnInit() {
    this.getMenu();
  }

}
