import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';

import { PizzasComponent } from './pizzas/pizzas.component'
import { PostresComponent } from './postres/postres.component';
import { EntrantesComponent } from './entrantes/entrantes.component';
import { DetalleMenuComponent } from './detalle-menu/detalle-menu.component';

const routes: Routes = [
  { path: '', component: PizzasComponent },
  { path: 'pizzas', component: PizzasComponent },
  { path: 'postres', component: PostresComponent },
  { path: 'entrantes', component: EntrantesComponent },
  { path: 'detalle-ficha/:id', component: DetalleMenuComponent}
]

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forRoot(routes)
  ],
  exports:[ 
    RouterModule
  ],
  declarations: []
})
export class AppRoutingModule { }
