import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { menuIndividual, menuColectivo } from './platos';
import { Platos } from './listaPlatos';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PlatosService {

 url: string = "./assets/categorias.json";

  constructor(private http:HttpClient) { }

  getMenus():Observable<menuColectivo[]>{
    return this.http.get<menuColectivo[]>(this.url)
  }

  getMenu(id:number):menuIndividual{
    return Platos.find(menu => menu.Plato.id == id);
  }
}
